var Logger = require('./logger');

var lg = Logger.getInstance();

var Response = function Response(res, options, onSuccess, onError) {
  var responseStr = '';
  res.setEncoding('utf8').on('data', function (str) {
    responseStr += str;
  }).on('end', function () {
    try {
      var obj = JSON.parse(responseStr);

      if (res.statusCode !== 200) {
        var next = function next() {
          return onError(obj);
        };

        if (!lg.enabled) {
          return next();
        }

        lg.log(next, options, obj);
      } else {
        var _next = function _next() {
          return onSuccess(obj);
        };

        if (!lg.enabled) {
          return _next();
        }

        lg.log(_next, options);
      }
    } catch (e) {
      var _next2 = function _next2() {
        return onError(obj);
      };

      if (!lg.enabled) {
        return _next2();
      }

      lg.log(_next2, options, e);
    }
  });
};

module.exports = Response;