var Logger = function () {
  var instance;

  function createInstance() {
    return {
      enabled: false,
      setLogging: function setLogging(state) {
        this.enabled = state;
      },
      log: function log(next, meta, err) {
        var metaStr = Object.keys(meta).reduce(function (a, c) {
          return "".concat(a, " ").concat(c, "=").concat(meta[c]);
        }, '');

        if (!err) {
          console.log('\x1b[32m', 'OK', '\x1b[0m', metaStr);
        } else {
          console.log('\x1b[31m', 'ERR', '\x1b[0m', metaStr);
          console.error(err);
        }

        next();
      }
    };
  }

  return {
    getInstance: function getInstance() {
      if (!instance) {
        instance = createInstance();
      }

      return instance;
    }
  };
}();

module.exports = Logger;